.map {
  position: relative;
}

.map__link {
  &::before {
    content: "";

    z-index: 1;
    position: absolute;
    top: 46%;
    left: 45.5%;

    display: block;
    width: 27px;
    height: 27px;

    background-image: url("../img/icon-map-marker.svg");
  }
}

.map__image {
  display: block;
  position: relative;
  width: 100%;
  height: 350px;
}

@media (min-width: @tablet-width) {
  .map__link {
    &::before {
      top: 46.8%;
      left: 48.2%;
    }
  }
}

@media (min-width: @desktop-width) {
  .map__link {
    &::before {
      top: 46.4%;
      left: 48.9%;
    }
  }
}
